.p-index-about {
  $this: &;
  padding-top: 4em;

  &__lead {
    text-align: center;
    margin: 2em 0;

    &:first-of-type {
      margin-top: 4em;
    }

    &:last-of-type {
      margin-bottom: 4em;
    }

    // ------------------------------
    // Outview
    // ------------------------------
    &[data-inview] {
      opacity: 0;
      transition-property: all;
      transition-duration: 0;
      transition-delay: 0;
      transform: translateY(2em) scale(.9);
    }

    // ------------------------------
    // Inview
    // ------------------------------
    &[data-inview].is-inview {
      opacity: 1;
      transform: translateX(0) translateY(0) scale(1);
      transition-duration: .6s;
      transition-delay: .1s;
    }

    @include ltXXS {
      text-align: left;
      padding: 0 2em;

      br {
        display: none;
      }
    };
  }

  &__detail {
    @include flex(ce,ce,false);
    padding: 6em 4em 0;
    margin-bottom: 8em;
    position: relative;

    &::before {
      @include fs(20);
      content: '● ● ● ● ●';
      display: block;
      position: absolute;
      text-align: center;
      background: red;
      top: -.5em;
      line-height: 1.5;
      letter-spacing: 1em;
      width: 10em;
      left: calc(50% - 5em);
      @include gradtypes;
      @include opacity(.5);

      @include ltSM {
        content: none;
        display: none;
      };
    }

    @include ltSM {
      padding: 0;
    };

    @include ltXS {
      display: block;
      padding: 0 4em;
    };

    @include ltXXS {
      display: block;
      padding: 0 2em;
      margin-bottom: 4em;
    };
  }

  &__thumb {
    width: 45%;
    min-width: 45%;
    margin-right: 4em;

    @include ltXS {
      width: 30em;
      max-width: 100%;
      margin: 0 auto 4em;
    };
  }

  &__text {

    @include ltXS {
      width: 38em;
      max-width: 100%;
      margin: 0 auto;
    };
  }

  &__image {
    display: block;
    border-radius: 1.6em;

    @include ltXXS {
      border-radius: 1em;
    };
  }

  // ------------------------------
  // Outview
  // ------------------------------
  &__detail[data-inview] {

    #{$this}__thumb,
    #{$this}__text,
    &::before {
      opacity: 0;
      transition-property: all;
      transition-duration: 0;
      transition-delay: 0;
    }
    #{$this}__thumb {transform: translateX(-20%) scale(.9);}
    #{$this}__text {transform: translateX(20%) scale(.9);}
    &::before {transform: translateY(3em) scale(.5);}

    @include ltXS {
      #{$this}__thumb {transform: translateY(2em) scale(.9);}
      #{$this}__text {transform: translateY(2em) scale(.9);}
    };
  }

  // ------------------------------
  // Inview
  // ------------------------------
  &__detail[data-inview].is-inview {

    #{$this}__thumb,
    #{$this}__text,
    &::before {
      opacity: 1;
      transform: translateX(0) translateY(0) scale(1);
      transition-duration: .8s;
    }
    #{$this}__thumb {transition-delay: .1s;}
    #{$this}__text {transition-delay: .2s;}
    &::before {opacity: .5;}
  }
}
