.p-index-place-list {
  $this: &;
  padding-top: 6em;
  padding-bottom: 6em;
  background: $charcoal;
  color: $truewhite;

  &__lead {
    width: 40em;
    max-width: 100%;
    padding: 0 2em;
    margin: 4em auto;
    @include fs(14);
    text-align: center;

    @include ltXXS {
      text-align: left;
    };

    // ------------------------------
    // Outview
    // ------------------------------
    &[data-inview] {
      opacity: 0;
      transition-property: all;
      transition-duration: 0;
      transition-delay: 0;
      transform: translateY(2em) scale(.8);
    }

    // ------------------------------
    // Inview
    // ------------------------------
    &[data-inview].is-inview {
      opacity: 1;
      transform: translateX(0) translateY(0) scale(1);
      transition-duration: .6s;
    }
  }

  &__body {
    @include flex(fs,fs);
    margin: 4em auto 2em;
    padding-left: 1.5em;
    padding-right: 1em;
  }

  &__item {
    @include flex(ce,fs,false);
    background: rgba($trueblack,.1);
    cursor: pointer;
    width: calc(25% - .5em);
    margin-right: .5em;
    margin-bottom: .5em;
    @include ease(.3s);
    border-radius: 1em;
    position: relative;
    overflow: hidden;

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      margin-top: -12.5vw;
      margin-left: -12.5vw;
      width: 25vw;
      height: 25vw;
      border-radius: 50%;
      background: $trueblack;
      @include ease(.5s);
      @include opacity(0);
      transform: scale(.5);
      @include gradbg;
    }

    @include hover {

      &::before {
        @include opacity(.7);
        transform: scale(1);
        top: 50%;
        left: 50%;
      }
    };

    @include ltSM {
      width: calc(100% / 3 - .5em);
    };

    @include ltXS {
      width: calc(50% - .5em);
    };

    @include ltXXS {
      width: calc(100% - .5em);
    };
  }

  &__logo {
    width: 5em;
    min-width: 5em;
    height: 5em;
    padding: 1em;
    position: relative;
    z-index: 2;
  }

  &__image {
    border-radius: .3em;
  }

  &__detail {
    width: calc(100% - 5.5em);
    left: 0;
    line-height: 1.4;
    position: relative;
    z-index: 2;
  }

  &__area {
    display: block;
    @include fs(10);
    @include ellipsis;
    @include opacity(.5);
  }

  &__name {
    display: block;
    @include fs(14);
    @include ellipsis;
  }

  &__cat {
    display: block;
    @include fs(10);
    @include ellipsis;
    @include opacity(.5);
    font-weight: bold;
  }

  @include gtXS {
    // ------------------------------
    // Outview
    // ------------------------------
    &__body[data-inview] {

      #{$this}__item {
        opacity: 0;
        transition-property: all;
        transition-duration: 0;
        transition-delay: 0;
        transform: translateY(4em) scale(.9);
      }
    }

    // ------------------------------
    // Inview
    // ------------------------------
    &__body[data-inview].is-inview {

      #{$this}__item {
        opacity: 1;
        transform: translateX(0) translateY(0) scale(1);
        transition-duration: .6s;
        &:nth-child(1) {transition-delay: .05s;}
        &:nth-child(2) {transition-delay: .10s;}
        &:nth-child(3) {transition-delay: .15s;}
        &:nth-child(4) {transition-delay: .20s;}
        &:nth-child(5) {transition-delay: .25s;}
        &:nth-child(6) {transition-delay: .30s;}
        &:nth-child(7) {transition-delay: .35s;}
        &:nth-child(8) {transition-delay: .40s;}
        &:nth-child(9) {transition-delay: .45s;}
        &:nth-child(10) {transition-delay: .50s;}
        &:nth-child(11) {transition-delay: .55s;}
        &:nth-child(12) {transition-delay: .60s;}
      }
    }
  };

  @include ltXXS {
    // ------------------------------
    // Outview
    // ------------------------------
    &__item[data-inview] {
      opacity: 0;
      transition-property: all;
      transition-duration: 0;
      transition-delay: 0;
      transform: translateX(-20%) scale(.8);
    }

    // ------------------------------
    // Inview
    // ------------------------------
    &__item[data-inview].is-inview {
      opacity: 1;
      transform: translateX(0) translateY(0) scale(1);
      transition-duration: .6s;
    }
  };

}
