#tinymce,
.c-editor-content {

  @include clearfix;

  > * {
    margin-top: 3rem;
    margin-bottom: 3rem;
    line-height: 2em;

    // ----------------------------------------
    // Phrasing Content
    // ----------------------------------------

    a {
      font-weight: 600;
      border-bottom: 1px dashed;
      color: $male;
      @include external;

      @include hover {
        color: $female;
      };
    }

    strong {
      font-weight: bold;
    }

    em {
      font-style: italic;
      margin-right: .2em;
    }

    code {
      background: $white;
      color: DarkCyan;
      @include fs(14);
      display: inline-block;
      padding: .1em 1em;
      vertical-align: baseline;
      font-weight: bold;
      border-radius: .3em;
      font-family: monospace;
    }
  }

  // ----------------------------------------
  // Heading
  // ----------------------------------------

  $fs: 36;
  $rate: 0.09;
  h2, h3, h4, h5, h6 {
    font-weight: 300;
    margin-top: 5rem;
    margin-bottom: 2.5rem;
    line-height: 1.2;

    & > small {
      font-weight: 400;
      font-size: 50%;
      @include opacity(.5);
    }

    &:first-child { margin-top: 0; }
  }

  h2 {
    font-size: 200%;
  }

  h3 {
    font-size: 180%;
  }

  h4 {
    font-size: 165%;
  }

  h5 {
    font-size: 140%;
  }

  h6 {
    font-size: 120%;
  }

  // ----------------------------------------
  // Paragraph
  // ----------------------------------------

  p {
    @include fs(14);
    color: $grey;
  }

  // ----------------------------------------
  // Table
  // ----------------------------------------

  table {

    tr {}

    th, td {
      border: 1px solid $lightsilver;
      padding: .5em 1em;
    }

    th {
      background: $white;
    }

    td {}
  }

  // ----------------------------------------
  // Horizontal Line
  // ----------------------------------------

  hr {
    display: block;
    border: none;
    height: 1px;
    background: rgba($grey,.1);
  }

  // ----------------------------------------
  // Lists
  // ----------------------------------------

  ul, ol {
    padding-left: 1.5em;
    @include fs(14);
    color: $grey;

    li {
      margin: 1em 0;
      line-height: 1.4;
    }
  }

  ul {
    list-style-type: disc;
  }

  ul,ol {

    > li ul,
    > li ol {
      // padding-left: 3em;
    }
  }

  ul ul,
  ol ul {
    list-style-type: circle;
  }

  ol ol ul,
  ol ul ul,
  ul ol ul,
  ul ul ul {
    list-style-type: square;
  }

  ol {
    display: block;
    list-style-type: decimal;
  }

  ol ol,
  ul ol {
    list-style-type: lower-alpha;
  }

  ul ul ol,
  ul ol ol,
  ol ul ol,
  ol ol ol {
    list-style-type: lower-roman;
  }

  // ----------------------------------------
  // Image
  // ----------------------------------------

  img {
    margin-top: 1em;
    margin-bottom: 1em;
  }

  // ----------------------------------------
  // Inline Frame
  // ----------------------------------------

  iframe {
    max-width: 100%;
  }

  // ----------------------------------------
  // Preformatted text
  // ----------------------------------------

  pre {
    font-family: monospace;
  }

  // ----------------------------------------
  // Blockquote
  // ----------------------------------------

  blockquote {
    font-size: 90%;
    border-left: 1em solid rgba($grey,.1);
    padding-left: 2em;
    clear: both;

    & > * {
      margin-top: 2rem;
      margin-bottom: 2rem;

      &:first-child {
        margin-top: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  // ----------------------------------------
  // フォーム
  // ----------------------------------------

  form {
    .require {
      display: inline-block;
      float: right;
      background: $male;
      color: $white;
      padding: 0 1em;
      border-radius: 1.5em;
      @include fs(12);
      font-weight: 600;
    }

    table {
      width: 100%;
      border-top: 1px solid $lightsilver;
      table-layout: fixed;

      @include ltXS {
        border-top: none;
      };

      th, td {
        border: none;
        border-bottom: 1px solid $lightsilver;

        @include ltXS {
          width: 100%;
          display: block;
        };
      }

      th {
        text-align: left;
        width: 20%;
        padding: 1.6em 1.6em 1.6em 0;
        font-weight: 500;
        border-right: 1px solid $lightsilver;
        background: transparent;
        color: $grey;

        @include ltXS {
          width: 100%;
          display: block;
          border-right: none;
          padding: 1em 0;
          margin-bottom: .4em;
          @include fs(18);
        };
      }

      td {
        width: 32%;
        padding: 1em 0 1em 2em;

        @include ltXS {
          width: 100%;
          display: block;
          padding: 0;
          border-bottom: none;
          margin-bottom: .4em;
          @include fs(18);
        };
      }
    }

    input[type=text],
    input[type=tel],
    input[type=email],
    input[type='password'] {
      display: inline-block;
      width: auto;
      border-radius: .5em;
      padding: .4em .4em;
      border: 1px solid $silver;
      @include fs(14);
      font-family: inherit;
      -webkit-appearance: button;

      &:-webkit-autofill {
        /* ここで指定しているbackground-colorは黄色い背景色のことです */
        -webkit-transition: background-color 9999s;
        transition: background-color 9999s;
      }

      @include ltXS {
        font-size: 16px;
        margin-bottom: .4em;
        max-width: 100% !important;
      };

      &.formInput {
        width: calc( 100% - 4em );

        @include gtSM {
          margin-left: .5em;
        };

        @include ltXS {
          width: 100%;
        };

        &--small {
          &1 { width: 1em; }
          &2 { width: 2em; }
          &3 { width: 3em; }
          &4 { width: 4em; }
          &5 { width: 5em; }
          &6 { width: 6em; }
          &7 { width: 7em; }
          &8 { width: 8em; }
          &9 { width: 9em; }
          &10 { width: 10em; }
          &11 { width: 11em; }
          &12 { width: 12em; }
          &13 { width: 13em; }
          &14 { width: 14em; }
          &15 { width: 15em; }
          &16 { width: 16em; }
          &17 { width: 17em; }
          &18 { width: 18em; }
          &19 { width: 19em; }
          &20 { width: 20em; }
          &21 { width: 21em; }
          &22 { width: 22em; }
          &23 { width: 23em; }
          &24 { width: 24em; }
          &25 { width: 25em; }
          &26 { width: 26em; }
          &27 { width: 27em; }
          &28 { width: 28em; }
          &29 { width: 29em; }
          &30 { width: 30em; }
        }

        &--block {
          display: block;
          width: 100%;
        }
      }
    }

    @include ltXS {
      textarea,
      input[size='40'],
      input[type=tel],
      input[type=email],
      input[type='password'] {
        width: 100% !important;
      }
    };

    input[type=submit],
    input[type=button] {
      color: $male;
      background: $truewhite;
      padding: .6em 1em;
      @include fs(16);
      border-radius: 2em;
      font-family: inherit;
      @include ease(.3s);
      min-width: 12em;
      border: none;
      box-shadow: 0 0 0 1px currentColor inset;

      @include ltXXS {
        min-width: 0;
        width: 50%;
        max-width: 12em;
        padding: 1em 1em;
      };

      @include hover {
        color: $truewhite;
        background-color: $male;
        box-shadow: none;
      };

      &:disabled {
        @include opacity(.5);
        cursor: no-drop;
      }
    }

    input[type=submit] {
      color: $truewhite;
      @include gradbg;
      box-shadow: none;

      @include hover {
        background-color: transparent;
        filter: hue-rotate(-30deg) brightness(120%);
      };
    }

    input[type=button].wpcf7-back {
      border-radius: 3em 0 0 3em;
      border-right: none;
    }

    input[type=submit].wpcf7-submit {
      border-radius: 0 3em 3em 0;
    }

    select {
      display: inline-block;
      cursor: pointer;
      -webkit-appearance: button;
      -moz-appearance: button;
      border-radius: .5em;;
        border: 1px solid $silver;
      font-size: 12px;
      padding: .5em 2em .5em .8em;
      background-color: $white;
      background-image: url('../img/form-parts/_part-select.png');
      background-size: 18px auto;
      background-repeat: no-repeat;
      background-position: right center;
      margin-bottom: 0;
      font-family: inherit;

      @include ltXS {
        font-size: 16px;
        width: 100%;
      };
    }

    .formText {
      text-align: right;
      display: inline-block;
      width: 2.4em;

      @include ltXS {
        text-align: left;
      };
    }

    textarea {
      width: 100%;
      max-width: 100% !important;
      border-radius: .5em;;
      padding: .4em .4em;
      border: 1px solid $silver;
      @include fs(14);
      font-family: inherit;
      -webkit-appearance: button;

      @include ltXS {
        font-size: 16px;
      };
    }
  }

  // ----------------------------------------
  // パスワード保護フォーム
  // ----------------------------------------

  form.post-password-form {
    background: rgba($grey,.1);
    border-radius: 1em;
    padding: 3em 3em;
    text-align: center;

    p {

      &:nth-child(2) {
        margin-bottom: 2em;
      }

      &:nth-child(2) {
        width: auto;
        max-width: 30em;
        margin: 2em auto 1em;
        @include flex(ce,ce);
        background: $truewhite;
        border-radius: 1em;
        padding: 3em 4em;
      }

      label {
        display: block;
        margin-bottom: 2em;
      }

      input[type='password'] {}

      input[type='submit'] {
        width: 100%;
      }
    }

  }

}

// ----------------------------------------
// Post Header
// ----------------------------------------
.c-editor-header {
  padding: 1em 0;
  border-bottom: 2px solid rgba($grey,.2);
  margin-bottom: 2em;

  &__postinfo {
    @include flex(ce);
    color: $grey;
  }

  &__date {
    @include fs(14);
    @include flex(ce);
  }

  &__date-body {
    @include fs(12);
    margin-left: .2em;
    font-weight: 700;
  }

  &__en-title {
    @include fs(12);
    font-weight: bold;
    color: $lightgrey;
    text-transform: uppercase;
  }

  &__title {
    @include fs(30);
    font-weight: 400;
    color: $darkgrey;
  }

  &__cat {
    margin-left: 1em;
    @include fs(14);
    text-transform: uppercase;
    @include flex(ce);

    ul {
      margin-left: .5em;
      @include flex(ce);
    }
  }

  &__tag {
    @include fs(12);
    color: $grey;
    @include flex(ce);

    a {
      display: inline-block;
      padding: 0 1em;
      border: 1px solid rgba($grey,.2);
      border-radius: .5em;;
      margin: .2em;
      color: currentColor;

      @include hover {
        background: $lightgrey;
        color: $black;
        border-color: transparent;
      };
    }
  }

  &__tag-label {
    @include fs(13);
    text-transform: uppercase;
  }

  &__tag-body {}

  &__author {
    @include fs(14);
    @include flex(ce);
    margin-bottom: .5em;

    img {
      max-width: 3em;
      min-width: 3em;
      height: 3em;
      margin-right: .5em;
    }
  }

  &__taxonomies {
    @include fs(12);
    color: $grey;
    @include flex(ce);
    margin-top: 1em;
  }

  &__taxonomy {
    @include flex(ce);
  }

  &__tax-name {
    border: 1px solid rgba($grey,.2);
    border-radius: 1em;
    padding: 0 1em;
    @include fs(10);
    font-weight: 600;
  }

  &__term-name {
    padding: 0 0.5em;

    a {
      color: currentColor;
    }
  }

  &__duration {
    @include fs(12);
    color: $grey;
    margin-top: 1em;
  }
}

#tinymce {
  padding-left: 2em;
  padding-right: 2em;
  background: $truewhite;
}
