.p-index-usage {
  $this: &;
  padding-top: 4em;
  padding-bottom: 4em;
  background: $white;

  &__intro {
    width: 40em;
    max-width: 100%;
    padding: 0 2em;
    margin: 4em auto;
    @include fs(14);
    text-align: center;

    @include ltXXS {
      text-align: left;
    };

    // ------------------------------
    // Outview
    // ------------------------------
    &[data-inview] {
      opacity: 0;
      transition-property: all;
      transition-duration: 0;
      transition-delay: 0;
      transform: translateY(2em) scale(.8);
    }

    // ------------------------------
    // Inview
    // ------------------------------
    &[data-inview].is-inview {
      opacity: 1;
      transform: translateX(0) translateY(0) scale(1);
      transition-duration: .6s;
    }
  }

  &__body {
    margin: 6em auto 0;
  }

  &__item {
    background: $truewhite;
    @include flex(ce,fs,false);
    position: relative;
    width: 58em;
    max-width: 100%;
    margin: 5.4em auto;

    @include gtSM {
      border-radius: 2em;
    };

    @include ltXXS {
      flex-wrap: wrap;
      margin: 4em auto;
    };

    &::after {
      content: '';
      display: block;
      width: 0;
      height: 0;
      position: absolute;
      bottom: -1.2em;
      left: calc(50% - 2em);
      border-top: 1.2em solid $truewhite;
      border-left: 2em solid transparent;
      border-right: 2em solid transparent;
    }

    &:last-child::after {
      content: none;
      display: none;
    }
  }

  &__icon {
    display: block;
    width: 6.8em;
    height: 6.8em;
    position: absolute;
    top: -3.4em;
    left: calc(50% - 3.4em);
    background: $truewhite;
    border-radius: 50%;
    z-index: 1;

    @include ltXXS {
      width: 6em;
      height: 6em;
      top: -2.4em;
      left: calc(50% - 3em);
      padding: .2em;
    };
  }

  &__svg {
    width: 100%;
    height: 100%;
    display: block;
  }

  &__thumb {
    width: 21em;
    min-width: 21em;
    height: 21em;

    @include gtSM {
      border-radius: 2em 0 0 2em;
      overflow: hidden;
    };

    @include ltXS {
      width: 50%;
      min-width: 50%;
      height: 20em;
    };

    @include ltXXS {
      width: 100%;
      min-width: 100%;
      height: 65vw;
      padding: 1.4em 1.4em 0;
    };
  }

  &__image {
    display: block;
    width: 100%;
    height: 100%;
    @include object-fit;

    @include gtSM {
      @include safari {
        border-radius: 2em 0 0 2em;
      };
    };

    @include ltXXS {
      border-radius: 1em;
    };
  }

  &__detail {
    padding: 2em 4em;

    @include ltXS {
      padding: 1em 2em;
    };

    @include ltXXS {
      padding: 2em;
    };
  }

  &__title {
    @include fs(20);
    margin-bottom: 2em;

    @include ltXXS {
      margin-bottom: 1em;
    };
  }

  &__lead {
    @include fs(14);
    line-height: 2;
  }

  // ------------------------------
  // Outview
  // ------------------------------
  &__item[data-inview] {
    opacity: 0;
    transition-property: all;
    transition-duration: 0;
    transition-delay: 0;
    transform: translateY(2em) scale(.8);

    #{$this}__image,
    #{$this}__icon,
    #{$this}__title,
    #{$this}__lead {
      opacity: 0;
      transition-property: all;
      transition-duration: 0;
      transition-delay: 0;
    }
    #{$this}__image {transform: scale(1.6);}
    #{$this}__icon {transform: translateX(2em);}
    #{$this}__title {transform: translateX(2em);}
    #{$this}__lead {transform: translateX(2em);}

    @include ltXS {
      #{$this}__image {transform: translateX(-20%);}
    };
  }

  // ------------------------------
  // Inview
  // ------------------------------
  &__item[data-inview].is-inview {
    opacity: 1;
    transform: translateX(0) translateY(0) scale(1);
    transition-duration: .6s;

    #{$this}__image,
    #{$this}__icon,
    #{$this}__title,
    #{$this}__lead {
      opacity: 1;
      transform: translateX(0) translateY(0) scale(1);
      transition-duration: .6s;
    }
    #{$this}__icon {transition-delay: .1s;}
    #{$this}__title {transition-delay: .3s;}
    #{$this}__lead {transition-delay: .5s;}
  }

}
