#### Breakpoints

プロジェクトで使用するブレイクポイントはビューポートの横幅を基準に 5 つ定義しています。（スタイル指定対象範囲: 6）<br>
これらのブレイクポイントを基準に Mixin やユーティリティ・クラスを作成する場合は、 `@mixin flex-ltXS {...}` や `.u-hidden-xxs {...}` 、 `.u-visible-gtSM {...}` などの様に、基準範囲を明示するためにサフィックスで追加すると良いでしょう。

