#### Mixins for Media Queries

下記の Mixin を使用することで、各ブレイクポイントを対象としたメディアクエリを簡単に記述できます。

##### 対象サイズ未満

```scss
// XXSサイズ未満
@include ltXXS {
  // content
}

// XSサイズ未満
@include ltXS {
  // content
}

// SMサイズ未満
@include ltSM {
  // content
}

// MDサイズ未満
@include ltMD {
  // content
}

// LGサイズ未満
@include ltLG {
  // content
}

// XLサイズ未満（ = 全てのサイズ ）
@include ltXL {
  // content
}
```

##### 対象サイズ以上

```scss
// XXSサイズ以上（ = 全てのサイズ ）
@include gtXXS {
  // content
}

// XSサイズ以上
@include gtXS {
  // content
}

// SMサイズ以上
@include gtSM {
  // content
}

// MDサイズ以上
@include gtMD {
  // content
}

// LGサイズ以上
@include gtLG {
  // content
}

// XLサイズ以上
@include gtXL {
  // content
}
```

##### 対象サイズ範囲内

```scss
// XXSサイズ範囲内
@include inXXS {
  // content
}

// XSサイズ範囲内
@include inXS {
  // content
}

// SMサイズ範囲内
@include inSM {
  // content
}

// MDサイズ範囲内
@include inMD {
  // content
}

// LGサイズ範囲内
@include inLG {
  // content
}

// XLサイズ範囲内
@include inXL {
  // content
}
```

##### ビューポートの向きの判別

```scss
// 横向き
@include landscape {
  // content
}

// 縦向き
@include portrait {
  // content
}
```

#### ユーティリティ・クラスおよび Mixin・変数の命名に関して

ブレイクポイントを使用したユーティリティ・クラスおよび Mixin 、変数を作成する場合は、対象となるブレイクポイントおよび範囲を明確にするために以下の項目を命名規則としています。

1. **範囲の指定** <br>
  - lt* : 対象のブレイクポイント以下（less than）
  - gt* : 対象のブレイクポイント以上（greater than）
  - in* : 対象の範囲内（in range）
1. **ブレイクポイントの指定** <br>
  範囲の指定のサフィックスに続けて、ブレイクポイントのサフィックスを **大文字で付与** します。

```scss
// e.g.
@mixin flex-ltXS {...}
.u-visible-gtSM {...}
```

> ※ただし、ユーティリティ・クラスの場合かつ対象の範囲内である場合のみ、範囲の指定である `in*` の省略と、ブレイクポイントのサフィックスを小文字で記すことを許容します。
>
> ```scss
> // e.g.
> // .u-hidden-inXXS {...} =>
> .u-hidden-xxs {...}
> ```

#### ユーティリティ・クラスのための Media Queries ループ

Media Queries ごとにループしたい場合、簡単にサフィックス付きのユーティリティ・クラスを生成できます。

###### mq

ブレイクポイントのサフィックスに対応するメディアクエリの範囲内にスタイルを指定する場合に使用します。

```scss
.u-hidden {
  @include mq {display: none;};
}
```
