.p-confirm-notice {
  position: relative;

  &__box {
    border: 2px solid $male;
    margin-bottom: 2em;
    padding: 2em;
    border-radius: 1em;
  }

  &__title {
    @include fs(18);
    padding: .4em 0;
    margin: 0 auto -1em;
    color: $grey;
    background: $male;
    color: $truewhite;
    border-radius: 1.5em;
    text-align: center;
    width: 100%;
    max-width: 12em;
    position: relative;
  }

  &__subTitle {
    font-weight: 600;
    @include fs(16);
    text-align: center;
    color: $male;
  }

  &__lead {
    @include fs(12);
    margin: 1.5em 0 0;
    line-height: 1.6;
  }
}
