.c-pagetop-button {
  display: block;
  width: 3.6em;
  height: 3.6em;
  position: fixed;
  bottom: 1em;
  right: -5em;
  text-align: center;
  color: rgba($male,1);
  transition-duration: .8s;
  border-radius: 1.8em;
  z-index: 990;

  @include ltSM {
    @include gradbg;
    color: $truewhite;
    // box-shadow: 0 0 1em rgba($trueblack,.2);
  };

  &.is-active {
    right: 1em;
  }

  &.is-absolute {
    position: absolute;

    @include ltXS {
      width: calc(100% - 2em);
      right: 1em;
    };
  }

  &::before {
    content: "";
    display: inline-block;
    width: 1em;
    height: 1em;
    margin-top: 1.4em;
    border-left: 2px solid;
    border-top: 2px solid;
    transform: rotate(45deg);
    position: relative;
    z-index: 2;
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    @include gradbg;
    border-radius: 1.8em;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    @include opacity(0);
    @include ease(.3s);
    transform: scale(0);
  }

  @include hover {
    color: $truewhite;

    &::after {
      @include opacity(1);
      transform: scale(1);
    }
  };
}
