.p-index-keyvisual {
  $this: &;
  position: relative;
  width: 100%;
  height: 100vh;
  max-height: 100vh;
  z-index: 1;
  overflow: hidden;
  background: rgba($trueblack,.5);
  background-image: url('../img/index/key-visual.jpg');
  background-size: cover;
  background-attachment: fixed;

  @include gtSM {
    background-position: center;
  };

  @include ltXS {
    height: 100%;
    max-height: 180vw;
    background-size: auto 100%;
    background-attachment: scroll;

    @include landscape {
      height: 70vw;
    };
  };

  // @include ltXXS {
  //   height: auto;
  //   max-height: none;
  // };

  &__container {
    @include flex(ce,ce);
    height: 100%;
  }

  &__item {
    color: $truewhite;

    @include ltXS {
      padding: 0 2em;
    };

    @include ltXXS {
      width: 100%;
      padding: 0;
      margin-top: 4em;
      margin-bottom: 4em;
    };
  }

  &__logo {
    @include fs(40);

    @include ltXS {
      @include fs(36);
    };

    .c-logo__symbol {
      width: 1em;
    }
  }

  &__detail {
    margin-top: 2em;
    @include fs(16);
    width: 35em;
    background: rgba($trueblack,.4);
    border-radius: 1.6em;
    padding: 2.5em 2.5em;

    @include ltXS {
      background: rgba($truewhite,.9);
      color: $lightcharcoal;
    };

    @include ltXXS {
      margin-top: 4em;
      width: 100%;
    };
  }

  &__lead {
    @include fs(14);
  }

  &__button {
    max-width: 20em;
    margin: 1.5em auto .5em;
    text-shadow: none;

    @include hover {
      color: $truewhite;
    };
  }

  // ------------------------------
  // Outview
  // ------------------------------
  &__item[data-inview] {

    #{$this}__logo-body,
    #{$this}__logo-name,
    #{$this}__author,
    #{$this}__detail {
      opacity: 0;
      transition-property: all;
      transition-duration: 0;
      transition-delay: 0;
    }
    #{$this}__logo-body {transform: translateY(1em) scale(.9);}
    #{$this}__logo-name {transform: translateY(.5em) scale(.9);}
    #{$this}__author {transform: scale(.9);}
    #{$this}__detail {transform: translateY(-1em) scale(.9);}
  }

  // ------------------------------
  // Inview
  // ------------------------------
  &__item[data-inview].is-inview {

    #{$this}__logo-body,
    #{$this}__logo-name,
    #{$this}__author,
    #{$this}__detail {
      opacity: 1;
      transform: translateX(0) translateY(0) scale(1);
      transition-duration: .8s;
    }
    #{$this}__logo-body {transition-delay: .1s; opacity: .8;}
    #{$this}__logo-name {transition-delay: .2s;}
    #{$this}__author {transition-delay: .3s; opacity: .5;}
    #{$this}__detail {transition-delay: .4s;}
  }
}
