.l-footer-nav {
  $border: 1px solid rgba($grey,.1) !default;
  @include flex(ce,ce);
  width: 100%;
  margin-top: 4em;
  margin-bottom: 8em;

  @include ltXS {
    margin-bottom: 3em;
  };

  &__body {
    @include flex(ce,ce);

    @include ltXS {
      display: block;
      width: 100%;
    };
  }

  &__item {
    margin-left: 1em;
    margin-right: 1em;

    @include ltXS {
      border-bottom: $border;

      &:first-child {
        border-top: $border;
      }
    };
  }

  &__link {
    font-weight: 400;
    display: block;
    position: relative;
    @include opacity(.8);
    white-space: nowrap;

    &::before {
      content: '';
      display: block;
      position: absolute;
      bottom: -.3em;
      left: 50%;
      width: 0;
      height: .3em;
      border-radius: .2em;
      background: currentColor;
      @include ease(.3s);
      @include opacity(0);
    }

    & > .glyph:first-child {
      margin-right: .5em;
      @include opacity(.5);
    }

    @include hover {
      @include opacity(1);

      &::before {
        width: 100%;
        left: 0;
        @include opacity(.2);
      }
    };

    @include ltXS {
      padding: 1em 0;
      @include flex(ce,fs);
      display: flex;
      align-items: center;
      justify-content: space-between;

      &::after {
        content: "";
        display: block;
        width: .5em;
        height: .5em;
        border-top: 1px solid;
        border-right: 1px solid;
        margin-right: .2em;
        margin-left: auto;
        transform: rotate(45deg);
        @include opacity(.5);
      }
    };
  }
}
