.p-index-point {
  $this: &;
  background: $white;
  padding-top: 4em;
  padding-bottom: 4em;

  &__body {
    @include flex(st,sb);
    margin-top: 4em;
    margin-bottom: 2em;
  }

  &__item {
    width: 24%;
    text-align: center;
    box-sizing: content-box;
    border-radius: 2em;
    margin-top: 4em;

    @include ltSM {
      width: 48%;
    };

    @include ltXXS {
      width: 100%;
      margin-bottom: 2em;
    };
  }

  &__icon {
    display: block;
    width: 8em;
    height: 8em;
    margin: 0 auto;
    border-radius: 50%;
    padding: 1.8em;
    margin-top: -4em;
    position: relative;
    background: $truewhite;
    @include gradbg;

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 2px;
      right: 2px;
      bottom: 2px;
      left: 2px;
      background: $truewhite;
      border: 2px solid rgba($truewhite,.5);
      border-radius: 50%;
      z-index: 1;
    }
  }

  &__svg {
    display: block;
    width: 100%;
    height: 100%;
    @include object-fit;
    position: relative;
    z-index: 2;
  }

  &__title {
    @include fs(18);
    font-weight: 500;
    line-height: 1.4;
    margin: 1em 0;
    color: $darkgrey;
  }

  &__lead {
    @include fs(14);
    line-height: 1.6;
    letter-spacing: 0;
    padding: 0 1.4em 2em;

    @include ltSM {
      max-width: 20em;
      margin: 0 auto;
    };

    @include ltXXS {
      max-width: 100%;
    };
  }

  @include gtXS {
    // ------------------------------
    // Outview
    // ------------------------------
    &__body[data-inview] {

      #{$this}__item {
        opacity: 0;
        transition-property: all;
        transition-duration: 0;
        transition-delay: 0;
        transform: translateY(4em) scale(.8);
      }
    }

    // ------------------------------
    // Inview
    // ------------------------------
    &__body[data-inview].is-inview {

      #{$this}__item {
        opacity: 1;
        transform: translateX(0) translateY(0) scale(1);
        transition-duration: .9s;
        &:nth-child(1) {transition-delay: .1s;}
        &:nth-child(2) {transition-delay: .2s;}
        &:nth-child(3) {transition-delay: .3s;}
        &:nth-child(4) {transition-delay: .4s;}
      }
    }
  };

  @include ltXXS {

    // ------------------------------
    // Outview
    // ------------------------------
    &__item[data-inview] {

      #{$this}__icon,
      #{$this}__title,
      #{$this}__lead {
        opacity: 0;
        transition-property: all;
        transition-duration: 0;
        transition-delay: 0;
      }
      #{$this}__icon {transform: translateY(-1em) scale(.8);}
      // #{$this}__title {transform: scale(.9);}
      #{$this}__lead {transform: translateY(1em);}
    }

    // ------------------------------
    // Inview
    // ------------------------------
    &__item[data-inview].is-inview {

      #{$this}__icon,
      #{$this}__title,
      #{$this}__lead {
        opacity: 1;
        transform: translateX(0) translateY(0) scale(1);
        transition-duration: 1s;
      }
      #{$this}__icon {transition-delay: .1s;}
      #{$this}__title {transition-delay: .2s;}
      #{$this}__lead {transition-delay: .3s;}
    }
  };
}
