.c-tab {
  margin-bottom: 2em;
}

.c-tab-trigger {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  &__item {
    padding: .2em 1em;
    background: transparent;

    &:hover {
      background: $trueblack;
    }

    &.is-active {
      background: $trueblack;
    }
  }
}
.c-tab-target {

  &__item {
    padding: .2em 1em;

    &.is-visible {
      background: $trueblack;
    }
  }
}
