.p-placedetail {

  &__body {
    width: 40em;
    max-height: calc(100vh - 6em);
    border-radius: 2.4em;
    @include scroll;

    @include ltMD {
      border-radius: 2em;
    };

    @include ltSM {
      border-radius: 1.8em;
    };

    @include ltXS {
      border-radius: 1.6em;
    };
  }

  &__round-button {
    @include fs(20);
    @include flex(ce,ce);
    width: 2.4em;
    height: 2.4em;
    background: rgba($grey,.4);
    border: none;
    position: absolute;
    font-weight: bold;
    z-index: 2;
    border-radius: 50%;
    top: .5em;
    right: .5em;
    line-height: 0;
    padding-bottom: .2em;

    @include ltMD {
      top: .5em;
      right: .5em;
    };

    @include ltSM {
      width: 2.2em;
      height: 2.2em;
    };

    @include hover {
      background: $grey;
    };
  }

  &__header {
    position: relative;
    border-radius: 2.4em 2.4em 0 0;
    overflow: hidden;

    @include ltMD {
      border-radius: 2em 2em 0 0;
    };

    @include ltSM {
      border-radius: 1.8em 1.8em 0 0;
    };

    @include ltXS {
      border-radius: 1.6em 1.6em 0 0;
    };
  }

  &__cover {
    display: block;
    position: relative;
    height: 20em;
  }

  &__cover-image {
    display: block;
    width: 100%;
    height: 100%;
    @include object-fit;
  }

  &__title {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    @include gradbg(rgba($trueblack,0),rgba($trueblack,.5),bottom);
    @include flex(ce,fs);
    padding: 4em 3em 1em;
    z-index: 1;
  }

  &__logo {
    width: 4em;
    height: 4em;
  }

  &__logo-image {
    display: block;
    width: 100%;
    height: 100%;
    @include object-fit;
    border-radius: .3em;
  }

  &__name {
    font-weight: 500;
    width: calc(100% - 5em);
    padding-left: 1em;

    & > * {
      display: block;
      @include ellipsis;
    }

    span {
      @include fs(20);
    }

    small {
      @include fs(14);
      font-weight: bold;
    }
  }

  &__about {
    color: $charcoal;
    padding: 2em 3em;
  }

  &__about-label {
    text-align: center;
    color: $lightgrey;
    font-weight: bold;
    @include fs(18);
  }

  &__about-lead {
    @include fs(14);
    padding-top: .5em;
    padding-bottom: 2em;
    border-bottom: 2px solid rgba($lightgrey,.2);
  }

  &__table {
    margin: 0 3em 2em;
    color: $charcoal;
    table-layout: fixed;

    a {
      color: $male;
      @include external;

      @include hover {
        color: $female;
      };
    }

    b {
      color: $lightgrey;
      margin-right: 1em;
    }

    i {
      @include opacity(.5);
      margin-right: .5em;
      font-weight: normal;

      &.scale {
        display: inline-block;
        transform: scale(1.2);
      }
    }

    th, td {
      @include fs(14);
      text-align: left;
      padding-top: .5em;
      padding-bottom: .5em;
    }

    th {
      color: $lightgrey;
      font-weight: bold;
      width: 30%;
    }
  }

  &__close {
    @include gradbg;
    @include flex(ce,ce);
    width: calc(100% - 2em);
    margin: 0 1em 1em;
    border-radius: 2em;
    padding: 1em;
    border: none;
    text-transform: uppercase;

    @include ltXS {
      // padding: 1.2em;
    };
  }
}
