.l-language {
  @include fs(16);
  @include flex(ce,fs,false);
  margin-left: 1em;

  @include gtMD {
    max-width: 0;
    overflow: hidden;
    @include opacity(0);
    @include ease(.5s);
  };

  @include ltXS {
    @include fs(20);
    margin-left: 0;
    margin-right: 0;
  };

  &__item {
    & > a {
      @include flex(ce,fs,false);
      @include opacity(.5);
      padding: 1em .5em;
      color: inherit;

      @include ltXS {
        @include opacity(.8);
      };

      & > img {
        width: 1.6em;
        height: 1.6em;
        display: block;
        margin-right: .4em;
        transform: scale(.8);
        @include ease;
      }

      & > span {
        display: block;
        white-space: nowrap;
      }

      @include hover {
        @include opacity(1);

        & > img {
          transform: scale(1);
        }
      };
    }
  }
}

.l-navigation__lang-item {
  @include hover {
    .l-language {
      @include opacity(1);
      max-width: 20em;
    }
  };
}
