$headerH: 4.5em !default;

.l-header {
  $this: &;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  background: transparent;
  color: $truewhite;
  transition: all .15s ease-in-out;
  height: $headerH;
  min-width: 320px;

  @include ltSM {
    height: 60px;
  };

  @include ltXS {
    transition-duration: .3s;
  };

  .c-logo__body {
    @include hover {
      color: rgba($truewhite,.7);
    };
  }

  @mixin fixedSettings {
    .l-navigation {
      color: $lightcharcoal;
    }

    .c-button--primary {
      color: $male;

      @include hover {
        color: $truewhite;
      };
    }

    .c-button--grad {
      color: $truewhite;
    }

    .c-logo__body {
      @include hover {
        filter: hue-rotate(-50deg) brightness(110%);
      };
    }

    .c-logo__symbol {
      color: $male;
    }

    .c-logo__text {
      @include gradtypes;
    }

    .c-nav-trigger {
      color: $female;
    }
  }

  &.is-fixed {
    background: $truewhite;
    color: inherit;
    @include fixedSettings;
    box-shadow: 0 0 1em rgba($trueblack,.1);
    // margin-top: 0;
    // transform: translateY(0);

    @include ltXS {
      @include opacity(1);
    };
  }

  &:not(.is-fixed) {
    @include ltXS {
      transform: translateY(-60px);
      @include opacity(0);
      background: $truewhite;
      transition-duration: .6s;
      @include fixedSettings;
    };
  }

  @include ltSM {
    .is-entire-nav-target & {
      background: $truewhite;
      @include fixedSettings;
    }
  };

  @include admin-bar {
    top: 32px;

    @media screen and ( max-width: 782px ) {
      top: 46px;
    }
  };

  &__logo {
    @include fs(25);
    margin-left: 2em;

    @include ltXS {
      margin-left: -.5em;
    };
  }

  &__body {
    height: 100%;
    @include flex(ce,sb);

    @include ltXS {
      justify-content: center;
      padding: 0 1.5em;
    };
  }
}

@media screen and (max-width: 600px) {
  #wpadminbar {
    @include admin-bar {
      position: fixed;
    }
  }
}
