.p-page-list {
  $root: &;

  &__logo {
    color: red;
    justify-content: flex-start;
  }

  &__logo-body {
    color: $grey;
  }

  &__title {
    @include fs(14);
    border-top: 1px solid rgba($grey,.1);
    font-weight: 400;

    & > a,
    & > span {
      color: rgba($darkgrey,.7);
      font-weight: inherit;

      & > i {
        @include opacity(.5);
        margin-right: .5em;
      }
    }
  }

  &__label {
    color: currentColor;
    display: block;
    padding-top: 1em;
    padding-bottom: 1em;
    font-weight: 600;
  }

  &__content {
    padding-top: .8em;
    padding-bottom: .8em;
    line-height: 1.2em;
    @include flex(ce,fs,false);
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    // @include ellipsis;

    @include glyph-before('\F029',.5) {
      margin-right: .5em;
    };
  }

  &__body {
    border-bottom: 1px solid rgba($grey,.1);

    &--child {
      margin-top: -.25em;
      padding-bottom: .5em;
    }
  }

  &__item {
    @include fs(12);
    width: 100%;
  }

  &__button {
    margin-top: 1em;

    @include hover {
      color: $truewhite;
    };
  }
}
