.c-scrool-box {
  $borderColor: rgba($grey,.2);
  border: 1px solid $borderColor;
  border-radius: .4em;
  overflow: hidden;

  &__header {
    padding: .4em 1em;
    border-bottom: 1px solid $borderColor;
  }

  &__body {
    $rate: 0.8;
    @extend #{&}--md;
    padding: .4em 1em;
    overflow-y: scroll;

    &--xl { max-height: ( 100vh * $rate ); }
    &--lg { max-height: ( 80vh * $rate ); }
    &--md { max-height: ( 50vh * $rate ); }
    &--sm { max-height: ( 40vh * $rate ); }
    &--xs { max-height: ( 32vh * $rate ); }
    &--xxs { max-height: ( 26vh * $rate ); }
  }

  &__footer {
    padding: .4em 1em;
    border-top: 1px solid $borderColor;
  }
}
