// --------------------------------------------------
// Basic styling
// --------------------------------------------------

html {
  $baseFontSize: 62.5%;
  font-size: $baseFontSize;
  height: 100%;

  @include ltMD {
    font-size: $baseFontSize * 0.84;
  };

  @include ltSM {
    font-size: $baseFontSize * 0.80;
  };

  @include ltXS {
    font-size: $baseFontSize * 0.75;
  };
}

body {
  @include Mplus;
  // @include Noto;
  @include fs(16);
  line-height: 1.8;
  min-width: 320px;
  min-height: 100vh;
  margin: 0 auto !important;
  position: relative;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: auto;
  background: $white;
  color: $charcoal;
  color: $lightcharcoal;
  height: 100%;
  -ms-overflow-style: none;
  scroll-behavior: smooth;

  &::-webkit-scrollbar {
    display: none;
  }

  &.is-entire-nav-target {

    @include ltXS {
      height: 100vh;
      overflow: hidden;
    };
  }

  &.is-invalid-scroll {
    height: 100vh;
    overflow: hidden;
  }
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

  @include ltXS {
    user-select: none;
  };
}

// --------------------------------------------------
// Flow content
// --------------------------------------------------

// --------------------------------------------------
// Phrasing content
// --------------------------------------------------

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  font-weight: bold;
  word-break: break-all;
  @include ease(.3s);

  @include hover {
    color: $charcoal;
  };
}

b,
strong {
  font-weight: bold;
}

small {
  font-size: 80%;
}

// --------------------------------------------------
// Form Element
// --------------------------------------------------

button,
input[type="button"],
input[type="submit"] {
  color: currentColor;
  -webkit-appearance: button;
  -moz-appearance: button;
  border: 1px solid;
  cursor: pointer;
  @include ease;
  @include fs(16);
  text-align: center;
}

input:focus,
textarea:focus,
keygen:focus,
select:focus { outline-offset: -2px; }
:focus { outline: none; }
a:focus { outline: none;text-decoration: none; }

main {

  // --------------------------------------------------
  // Only in Main Content
  // --------------------------------------------------

  a {
    color: rgba($charcoal,.6);

    @include hover {
      color: $charcoal;
    };
  }
}
