.c-page-header {
  $this: &;
  background: $white;
  position: relative;
  overflow: hidden;

  &--shift-to-bottom {

    #{$this}__item {
      margin-top: $headerH;
      margin-bottom: 1.5em;

      @include inXS {
        margin-top: $headerH + 3em;
      };
    }
  }

  &__item {
    @include flex(ce,ce,false);
    min-height: 16em;
    padding: 3em;
    position: relative;
    z-index: 10;

    @include ltXXS {
      @include flex(ce,ce);
    };
  }

  &__en-title {
    @include fs(60);
    font-weight: 200;
  }

  &__detail {
    margin-left: 4em;

    @include ltXXS {
      text-align: center;
      margin-left: 0;
      margin-bottom: 1em;
    };

    &--center {
      text-align: center;
      margin-left: 0;
    }
  }

  &__title {
    @include fs(18);
    font-weight: 500;
    @include opacity(.5);
    @include flex(ce,ce);

    &::before,
    &::after {
      content: '';
      display: block;
      width: 2em;
      height: 1px;
      background: currentColor;
      margin: 0 1em;
      @include opacity(.5);
    }

    &--large {
      @include fs(30);
    }
  }

  &__logo {
    max-width: 18em;
    margin: 0 auto 2em;

    @include ltXS {
      max-width: 200px;
      height: 5em;
      @include flex(ce,ce);
    };

    img {
      display: block;

      @include ltXS {
        max-height: 100%;
      };
    }
  }

  // &__lead {
  //   margin-top: 1em;
  //   @include fs(14);
  // }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 8;
  }
}
