.c-logo {

  &__body {
    @include flex(ce,ce);
    font-weight: normal;
    color: $truewhite;
  }

  &__symbol {
    font-size: 150%;
    margin-right: .2em;
  }

  &__text {
    display: block;
  }

  &__name {
    line-height: 1.4;
    @include fs(16);
    @include flex(ce,ce);
  }

  &__product-tagline {
    @include fs(14);
    font-weight: 600;
    margin-right: .5em;
    letter-spacing: 0;
  }

  &__product-name {
    @include fs(24);
    font-weight: 400;
  }

  &__author {
    @include flex(ce,ce);
    @include fs(12);
    font-weight: bold;
    @include opacity(.5);
    letter-spacing: 0;
  }
}
