// ----------------------------------------
//  Placement of images posted in WordPress
// ----------------------------------------

@include gtSM {

  img {
    height: auto;

    &.aligncenter {
      max-width: 100%;
      display: block;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 1em;
    }

    &.centered {
      max-width: 100%;
      display: block;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 1em;
    }

    &.alignright {
      max-width: calc( 50% - 25px );
      // padding: 4px;
      margin: 0 0 25px 25px;
      display: inline;
    }

    &.alignleft {
      max-width: calc( 50% - 25px );
      // padding: 4px;
      margin: 0 25px 25px 0;
      display: inline;
    }
  }

  .alignright {
    float: right;
  }

  .alignleft {
    float: left;
  }
};

@include ltXS {
  img {
    height: auto;
    &.aligncenter,
    &.centered,
    &.alignright,
    &.alignleft {
      max-width: 100%;
      display: block;
      margin: 10px auto;
    }
  }
};
