.l-navigation {
  $border: 1px solid rgba($grey,.1) !default;
  padding: 0 1em;
  height: 100%;
  @include flex(ce,ce);
  color: $truewhite;

  @include ltXS {
    @include flex(fs,ce);
    @include scroll;
    padding: 1em 2em;
  };

  @include inSM {
    @include flex(ce,ce);
    padding: 0 .5em;
  };

  &__body {
    padding: 0 .5em;
    font-weight: bold;

    @include ltMD {
      padding: 0;
    };

    @include ltXS {
      width: 100%;
      max-width: 500px;
      padding: 1em;
    };

    @include ltXXS {
      padding: .5em 0em;
    };
  }

  &__item {
    text-transform: uppercase;
    margin-left: .2em;
    margin-right: .2em;
    font-weight: inherit;

    a {
      color: inherit;

      @include hover {
        color: inherit;
      };
    }

    @include ltXS {
      margin: 0;

      &:nth-of-type(1) {
        border-top: $border;
      }
    };
  }

  &__link {
    @include fs(14);
    color: $darkgrey;
    font-weight: normal;
    text-decoration: none;
    position: relative;
    display: block;
    padding: .1em 0;
    font-size: inherit;
    @include opacity(.8);

    & > .glyph:first-child {
      margin-right: .5em;
      @include opacity(.5);
    }

    @include hover {
      color: $darkgrey;
    };

    @include gtSM {
      padding: .1em 0;
      margin-left: .5em;
      margin-right: .5em;

      &::before {
        content: '';
        display: block;
        position: absolute;
        bottom: -.3em;
        left: 50%;
        width: 0;
        height: .3em;
        border-radius: .2em;
        background: currentColor;
        @include ease(.3s);
        @include opacity(0);
      }
    };

    @include gtLG {
      margin-left: 1em;
      margin-right: 1em;
    };

    @include ltXS {
      padding: 1em 0;
      @include flex(ce,fs);
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: $border;

      &::after {
        content: "";
        display: block;
        width: .5em;
        height: .5em;
        border-top: 1px solid;
        border-right: 1px solid;
        margin-right: .2em;
        margin-left: auto;
        transform: rotate(45deg);
        @include opacity(.5);
      }
    };

    @include hover {
      @include opacity(1);

      &::before {
        width: 100%;
        left: 0;
        // width: 3em;
        // margin-left: -1.5em;
        @include opacity(.3);
      }
    };
  }

  &__button {
    margin-left: .4em;
    margin-right: .4em;

    @include hover {
      color: $truewhite;
    };

    @include ltXS {
      margin: 1em 0;
      @include fs(18);
    };
  }
}
