// ========================================
// Gradient
// ========================================

$grad1: $male;
$grad2: $female;

@mixin gradtypes($color1: $grad1, $color2: $grad2, $start: left) {
  color: $color1;
  background: -webkit-linear-gradient($start,$color1,$color2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@mixin gradbg($color1: $grad1, $color2: $grad2, $to:right) {
  background-image: linear-gradient(to $to, $color1 0%, $color2 100%);
}

// ========================================
//  IE Hack
// ========================================

@mixin opacity($transparent) {
  opacity: $transparent;
  filter: alpha(opacity= $transparent * 100);
}

@mixin clearfix {
  *zoom: 1;

  &::after {
    content: "";
    display: table;
    clear: both;
  }
}

@mixin object-fit($type:cover) {
  object-fit: $type;
  font-family: 'object-fit: #{$type}';
}

// ========================================
// Images
// ========================================

@mixin crisp {
  image-rendering: -webkit-optimize-contrast;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: crisp-edges;
  -ms-interpolation-mode: nearest-neighbor;
}

@mixin bgs($width,$height) {
  width: $width;
  height: $height;
  background-size: $width $height;
}

// ========================================
// Links & Hover action
// ========================================

@mixin transition($time:.15s) {
  transition: $time all;
}

@mixin ease($time:.15s) {
  transition: $time all ease-in-out;
}

@mixin linear($time:.15s) {
  transition: $time all linear;
}

@mixin hover {
  &:hover {
    @include gtMD { @content; }
  }
}

@mixin external($ct:'\2197',$family:glyph) {
  &[target]:not([href^='tel:']) {
    &::after {
      font-size: .8em;
      margin-left: .2em;
      margin-right: .2em;
    }
    @if $family == glyph {
      @include glyph-after($ct,.5);
    } @else if $family == far {
      @include far-after($ct,.5);
    } @else if $family == fab {
      @include fab-after($ct,.5);
    } @else if $family == fas {
      @include fas-after($ct,.5);
    }
  }
}

@mixin ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}


// ========================================
// Icons
// ========================================

@mixin icons-base($family) {
  font-family: $family;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: auto;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  line-height: 1;
  font-weight: 400;
}

@mixin far-before($ct,$op:1,$cl:inherit) {
  &::before {
    @include icons-base('Font Awesome 5 Free');
    content: '#{$ct}';
    opacity: $op;
    filter: alpha(opacity= $op * 100);
    color: $cl;
    @content;
  }
}

@mixin far-after($ct,$op:1,$cl:inherit) {
  &::after {
    @include icons-base('Font Awesome 5 Free');
    content: '#{$ct}';
    opacity: $op;
    filter: alpha(opacity= $op * 100);
    color: $cl;
    @content;
  }
}

@mixin fab-before($ct,$op:1,$cl:inherit) {
  &::before {
    @include icons-base('Font Awesome 5 Brands');
    content: '#{$ct}';
    opacity: $op;
    filter: alpha(opacity= $op * 100);
    color: $cl;
    @content;
  }
}

@mixin fab-after($ct,$op:1,$cl:inherit) {
  &::after {
    @include icons-base('Font Awesome 5 Brands');
    content: '#{$ct}';
    opacity: $op;
    filter: alpha(opacity= $op * 100);
    color: $cl;
    @content;
  }
}

@mixin fas-before($ct,$op:1,$cl:inherit) {
  &::before {
    @include icons-base('Font Awesome 5 Free');
    content: '#{$ct}';
    opacity: $op;
    filter: alpha(opacity= $op * 100);
    color: $cl;
    font-weight: 900;
    @content;
  }
}

@mixin fas-after($ct,$op:1,$cl:inherit) {
  &::after {
    @include icons-base('Font Awesome 5 Free');
    content: '#{$ct}';
    opacity: $op;
    filter: alpha(opacity= $op * 100);
    color: $cl;
    font-weight: 900;
    @content;
  }
}

@mixin glyph-before($ct,$op:1,$cl:inherit) {
  &::before {
    @include icons-base('glyphs');
    vertical-align: -.1em;
    content: '#{$ct}';
    opacity: $op;
    filter: alpha(opacity= $op * 100);
    color: $cl;
    @content;
  }
}

@mixin glyph-after($ct,$op:1,$cl:inherit) {
  &::after {
    @include icons-base('glyphs');
    vertical-align: -.1em;
    content: '#{$ct}';
    opacity: $op;
    filter: alpha(opacity= $op * 100);
    color: $cl;
    @content;
  }
}

// ========================================
// Flex Box
// ========================================

@mixin flex($ai:null,$jc:null,$fw:true) {
  display: flex;

  @if $ai {
    @if $ai == ce {
      align-items: center;
    } @else if $ai == fs {
      align-items: flex-start;
    } @else if $ai == fe {
      align-items: flex-end;
    } @else if $ai == st {
      align-items: stretch;
    } @else {
      align-items: $ai;
    }
  }

  @if $jc {
    @if $jc == ce {
      justify-content: center;
    } @else if $jc == fs {
      justify-content: flex-start;
    } @else if $jc == fe {
      justify-content: flex-end;
    } @else if $jc == sa {
      justify-content: space-around;
    } @else if $jc == sb {
      justify-content: space-between;
    } @else {
      justify-content: $jc;
    }
  }

  @if $fw == true {
    flex-wrap: wrap;
  } @else if $fw == false {
    flex-wrap: nowrap;
  } @else {
    justify-content: $fw;
  }
}

// ========================================
// Page Types
// ========================================

@mixin index() {
  .index & { @content; }
}

@mixin lower() {
  .lower & { @content; }
}

@mixin logged-in() {
  .logged-in & { @content; }
}

@mixin admin-bar() {
  .admin-bar & { @content; }
}

// ========================================
// Scroll
// ========================================

@mixin scroll {
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: none;
  scroll-behavior: smooth;

  &::-webkit-scrollbar {
    display: none;
  }
}
