.p-closing {
  padding: 6em 0;
  text-align: center;
  background: url(../img/index/bg-closing.jpg);
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
  color: $truewhite;
  position: relative;
  min-height: 100vh;
  @include flex(ce,ce);

  @include ltXS {
    padding-left: 1em;
    padding-right: 1em;
    background-size: auto 100%;
    background-attachment: scroll;
  };

  &::before {
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    background: $darkcharcoal;
    top: 0;
    left: 0;
    z-index: 0;
    @include opacity(.5);
  }

  & > * {
    position: relative;
    z-index: 1;
  }

  &__title {
    @include fs(60);
    font-weight: 200;
  }

  &__lead {
    margin-top: 1em;
    margin-bottom: 3em;
  }

  &__button {
    @include fs(30);
    display: inline-block;
    border: 2px solid rgba($truewhite,.5);
    padding: 1em 4em;
    color: $truewhite;

    @include ltXS {
      display: block;
      width: 100%;
      padding: 1em 0;
    };

    @include hover {
      background: $truewhite;
      color: $darkcharcoal;
    };
  }

  &__tel {
    @include fs(20);
    font-weight: bold;
    text-transform: uppercase;
    margin-top: 2em;

    @include ltXS {
      span { display: block; }
      i,br { display: none; }
    };
  }
}
