.p-fluid {
  $this: &;
  padding-top: 5em;
  padding-bottom: 5em;
  background-image: url('../img/index/fluid.jpg');
  background-position: center center;
  background-attachment: fixed;
  background-size: cover;
  color: $truewhite;
  position: relative;

  @include ltXS {
    background-size: auto 100%;
    background-attachment: scroll;
  };

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: $trueblack;
    z-index: 0;
    @include opacity(.3);
  }

  .c-heading--index {}

  .c-heading__label {
    color: $truewhite;
    background: none;
    -webkit-background-clip: inherit;
    -webkit-text-fill-color: inherit;
  }

  &__inner {
    position: relative;
    z-index: 1;
  }

  &__lead {
    margin-top: 4em;
    text-align: center;
    padding: 0 1em;

    @include ltXXS {
      text-align: left;
    };

    // ------------------------------
    // Outview
    // ------------------------------
    &[data-inview] {
      opacity: 0;
      transition-property: all;
      transition-duration: 0;
      transition-delay: 0;
      transform: translateY(2em) scale(.8);
    }

    // ------------------------------
    // Inview
    // ------------------------------
    &[data-inview].is-inview {
      opacity: 1;
      transform: translateX(0) translateY(0) scale(1);
      transition-duration: .6s;
    }
  }

  &__button {
    color: $truewhite;
    @include fs(20);
    width: 20em;
    margin: 2em auto;

    @include ltXS {
      max-width: 98%;
    };

    @include hover {
      background: $truewhite;
      color: #554A4B;
      box-shadow: none;
    };

    // ------------------------------
    // Outview
    // ------------------------------
    &[data-inview] {
      opacity: 0;
      transition-property: all;
      transition-duration: 0;
      transition-delay: 0;
      transform: translateY(2em) scale(.8);
    }

    // ------------------------------
    // Inview
    // ------------------------------
    &[data-inview].is-inview {
      opacity: 1;
      transform: translateX(0) translateY(0) scale(1);
      transition-duration: .6s;
    }
  }
}
