.l-footer {
  background: $white;
  color: $charcoal;
  padding-top: 8em;
  position: relative;
  z-index: 9;

  &::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: .3em;
    top: 0;
    left: 0;
    @include gradbg;
  }

  @include ltXXS {
    padding-top: 4em;
  };

  &__nav {
    @include flex(ce,ce);
  }

  a {
    color: currentColor;

    @include hover {
      color: currentColor;
    };
  }

  &__social {
    width: 100%;
    @include flex(ce,ce);
    margin-top: 4em;
  }

  &__social-inner {
    @include flex(ce,ce);
    transform: scale(1.3);

    @include ltXXS {
      transform: scale(1.1);
    };

    .addtoany_list a {
      margin: 0 2px;

      span {
        @include ease(.3s);
      }

      &:hover span {}

      @include hover {
        transform: scale(1.1);
      };
    }

    .a2a_svg svg {}
  }

  &__logo {
    @include fs(36);
    font-weight: normal;

    .c-logo__body {
      @include hover {
        filter: hue-rotate(-30deg) brightness(120%);
      };
    }

    .c-logo__body {
      @include hover {
        filter: hue-rotate(-50deg) brightness(110%);
      };
    }

    .c-logo__symbol {
      color: $male;
    }

    .c-logo__text {
      @include gradtypes;
    }
  }
}
