.c-nav-target {
  @include gtMD {
    height: 100%;
  };

  @include inSM {
    box-shadow: 0 .5em .25em rgba($trueblack,.1);
  };

  @include ltSM {
    display: block;
    background: $white;
    position: fixed;
    top: 60px;
    right: 0;
    left: 0;
    z-index: 1000;
    padding: 1em;
  };

  @include ltXS {
    bottom: 0;
    padding: 0;
  };

  @include mq {
    // height: 100%;
    display: none;
  };
}
