.c-heading {
  $this: &;

  &--index {
    text-align: center;

    // ------------------------------
    // Outview
    // ------------------------------
    &[data-inview] {

      & > * {
        opacity: 0;
        transition-property: all;
        transition-duration: 0;
        transition-delay: 0;

        &:nth-child(1) {transform: translateY(1em)}
        &:nth-child(2) {transform: translateY(1em)}
      }
    }

    // ------------------------------
    // Inview
    // ------------------------------
    &[data-inview].is-inview {

      & > * {
        opacity: 1;
        transform: translateX(0) translateY(0) scale(1);
        transition-duration: .6s;

        &:nth-child(1) {transition-delay: .1s;}
        &:nth-child(2) {transition-delay: .2s; opacity: .7;}
      }
    }

  }

  &__label {
    font-family: $Poiret;
    @include fs(50);
    display: block;
    letter-spacing: 0;
    @include gradtypes($male,$female,bottom);
  }

  &__text {
    @include fs(16);
    @include flex(ce,ce);
    @include opacity(.7);

    &::before,
    &::after {
      content: '';
      display: inline-block;
      width: 1em;
      height: 1px;
      background: currentColor;
    }

    &::before {
      margin-right: 1em;
    }

    &::after {
      margin-left: 1em;
    }
  }
}
