.c-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1020;
  font-size: 16px;

  &[data-modal] {
    display: none;
  }

  // &:not(.is-visible) {
  //   display: none;
  // }

  &__overlay {
    @include flex(ce,ce);
    width: 100%;
    height: 100%;
    padding: 20px;
    background: rgba($darkcharcoal,.97);

    @include ltXXS {
      padding: 4vw;
    };
  }

  &__inner {
    background: $base;
    box-shadow: 0 0 20px 0 rgba($trueblack,.3);
    transform: translateY(1em) scale(.9);
    @include opacity(0);
    transition-duration: 1s;

    &.is-visible {
      @include opacity(1);
      transform: translateY(0) scale(1);
    }
  }
}
