#cookie-law-info-again {
  @include ltXS {
    right: 5.5em !important;
    left: 5.5em;
  };
}

#cookie_hdr_showagain {
  @include MarcellusSC;
}

#cookie-law-info-bar {
  letter-spacing: 0;
  padding: 0 !important;

  & > span {
    @extend .c-container ;
    display: block;
    text-align: left;
    position: relative;
    margin-top: 2em;
    margin-bottom: 2em;

    @include gtSM {
      padding-right: 16em;
    };
  }

  #CONSTANT_OPEN_URL {
    @include external;

    @include hover {
      text-decoration: underline;
    };
  }

  .cli-plugin-main-button {
    @include gtSM {
      position: absolute;
      top: 50%;
      right: 0;
      margin-top: -1.5em;
    };

    text-align: center;
    margin-top: 1em;
    margin-left: 0;
    height: 3em;
    line-height: 3em;
    display: block;
    padding: 0 2em;
    @include fs(20);
    border-radius: 0;
    background: transparent !important;
    border: 1px solid $truewhite;

    @include hover {
      background: $truewhite !important;
      color: $charcoal !important;
    };
  }
}
