.c-button {
  font-weight: normal;
  border-radius: 2em;
  padding: .4em 2em;
  display: block;
  box-sizing: border-box;
  box-shadow: 0 0 0 1px currentColor inset;
  @include ease(.3s);
  text-align: center;

  @include ltXS {
    padding: .6em 2em;
  };

  & > .glyph {
    margin-right: .5em;
    @include opacity(.5);
  }

  &--primary {
    @extend .c-button ;
    color: $male;

    @include hover {
      background-color: $male;
      color: $truewhite;
      box-shadow: none;
    };
  }

  &--grad {
    @extend .c-button ;
    @include gradbg;
    color: $truewhite;
    box-shadow: none;

    @include hover {
      background-color: transparent;
      filter: hue-rotate(-30deg) brightness(120%);
    };
  }
}
