.l-main {
  div.wpcf7 {
    margin: 0;
    padding: 0;
  }

  div.wpcf7-response-output {
    margin: 2em 0.5em 1em;
    padding: 0.2em 1em;
    border-radius: 1.5em;
  }

  div.wpcf7 .screen-reader-response {
    position: absolute;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px);
    height: 1px;
    width: 1px;
    margin: 0;
    padding: 0;
    border: 0;
  }

  div.wpcf7-mail-sent-ok {
    border: 2px solid $male;
  }

  div.wpcf7-mail-sent-ng {
    border: 2px solid #ff0000;
  }

  div.wpcf7-spam-blocked {
    border: 2px solid #ffa500;
  }

  div.wpcf7-validation-errors {
    border: 2px solid $female;
  }

  .wpcf7-form-control-wrap {
    position: relative;
  }

  span.wpcf7-not-valid-tip {
    color: $female;
    font-weight: bold;
    display: block;
    width: 100%;
    @include fs(12);
    padding: 0 .4em;
  }

  .use-floating-validation-tip span.wpcf7-not-valid-tip {
    position: absolute;
    top: 20%;
    left: 20%;
    z-index: 100;
    border: 1px solid #ff0000;
    background: #fff;
    padding: .2em .8em;
  }

  span.wpcf7-list-item {
    display: inline-block;
    margin: 0 0 0 1em;
  }

  span.wpcf7-list-item-label::before,
  span.wpcf7-list-item-label::after {
    content: " ";
  }

  .wpcf7-display-none {
    display: none;
  }

  div.wpcf7 .ajax-loader {
    visibility: visible;
    display: inline-block;
    width: 120px;
    height: 30px;
    background-image: url('../img/common/ajax-loader.gif');
    background-size: 120px 30px;
    background-repeat: no-repeat;
    background-position: center center;
    border: none;
    padding: 0;
    margin: 0 0 0 4px;
    vertical-align: middle;
    position: absolute;
    top: -10px;
    left: 50%;
    margin-left: -60px;
    @include ease(.3s);
    @include opacity(0);
  }

  div.wpcf7 .ajax-loader.is-active {
    visibility: visible;
    @include opacity(1);
  }

  div.wpcf7 div.ajax-error {
    display: none;
  }

  div.wpcf7 .placeheld {
    color: #888;
  }

  div.wpcf7 .wpcf7-recaptcha iframe {
    margin-bottom: 0;
  }

  div.wpcf7 input[type="file"] {
    cursor: pointer;
  }

  div.wpcf7 input[type="file"]:disabled {
    cursor: default;
  }

  form {
    input[type=text],
    input[type=tel],
    input[type=email],
    textarea,
    select {
      &.wpcf7c-conf {
        border: none;
        background-color: rgb(250, 255, 189);
        transition: none;
        background-image: none;
      }
    }
    input:-webkit-autofill,
    textarea:-webkit-autofill,
    select:-webkit-autofill {
      &.wpcf7c-conf {
        background-color: rgb(250, 255, 189);
        transition: none;
        background-image: none;
      }
    }
  }

  .wpcf7-form {
    $adminbarH: 32px;
    margin-top: calc(-#{$headerH} - 1em);
    padding-top: calc(#{$headerH} + 1em);

    @include admin-bar {
      margin-top: calc(-#{$headerH} - #{$adminbarH} - 1em);
      padding-top: calc(#{$headerH} + #{$adminbarH} + 1em);

      @media screen and ( max-width: 782px ) {
        margin-top: calc(-#{$headerH} - #{$adminbarH} - 14px - 1em);
        padding-top: calc(#{$headerH} + #{$adminbarH} + 14px + 1em);
      }
    };

    @include ltXS {
      margin-top: calc(-60px - 1em);
      padding-top: calc(60px + 1em);

      @include admin-bar {
        margin-top: calc(-60px - #{$adminbarH} - 14px - 1em);
        padding-top: calc(60px + #{$adminbarH} + 14px + 1em);
      };
    };
  }
}
