body > .smart-page-loader {
  background-image: linear-gradient(45deg, $male, $female);
}


.p-page-loader {
  $this: &;
  width: 100%;
  height: 100%;
  @include flex(ce,ce);
  color: rgba($truewhite,.8);

  &__item {
    padding-bottom: 6em;
  }

  &__body {
    @include fs(40);
  }

  &__symbol {}

  &__text {}

  &__name {}

  &__author {
    color: rgba($truewhite,.5);
  }

  &__spinner {
    @include fs(20);
    width: 4em;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: 0;
    margin-left: -2em;
    @include flex(ce,sb);
  }

  &__spinner-item {
    width: 1em;
    height: 1em;
    background: rgba($truewhite,.9);
    border-radius: 100%;
    display: inline-block;
    -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
    animation: sk-bouncedelay 1.4s infinite ease-in-out both;

    &:nth-child(1) {
      -webkit-animation-delay: -0.32s;
      animation-delay: -0.32s;
    }

    &:nth-child(2) {
      -webkit-animation-delay: -0.16s;
      animation-delay: -0.16s;
    }
  }

  // ------------------------------
  // Outview
  // ------------------------------
  &[data-inview] {

    #{$this}__text,
    #{$this}__symbol,
    #{$this}__name,
    #{$this}__author {
      opacity: 0;
      transition-property: all;
      transition-duration: 0;
      transition-delay: 0;
    }
    #{$this}__text {
      max-width: 0;
      overflow: hidden;
    }
    #{$this}__symbol {
      transform: scale(3);
    }
    #{$this}__name {transform: translateX(-30%) translateY(20%) scale(1);}
    #{$this}__author {transform: translateX(30%) translateY(20%) scale(1);}

    #{$this}__spinner {}
  }

  // ------------------------------
  // Inview
  // ------------------------------
  &[data-inview].is-inview {

    #{$this}__text,
    #{$this}__symbol,
    #{$this}__name,
    #{$this}__author {
      opacity: 1;
      transform: translateX(0) translateY(0) scale(1);
      transition-duration: 1.2s;
      white-space: nowrap;
    }
    #{$this}__text {
      max-width: 10em;
      transition-delay: .5s;
    }
    #{$this}__symbol {transition-delay: .2s;}
    #{$this}__name {transition-delay: .6s;}
    #{$this}__author {transition-delay: 1s;}

    #{$this}__spinner {
      opacity: 0;
      @include ease(.6s);
    }
  }
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% { -webkit-transform: scale(0) }
  40% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  } 40% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
  }
}
