.p-scale-slider {
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;

  &__item {
    opacity: 0;
    transform: scale(1);

    // transform は FadeTime + IntarvalTime + α秒
    transition: opacity 3s linear, transform 16.5s linear;
    position: relative;
    z-index: -1;

    width: 100%;
    height: 100%;

    &:not(:first-child) {
      position: absolute;
      top: 0;
      left : 0;
      z-index: -1;
    }

    &.show_ {
      opacity: 1;
    }

    &.zoom_ {
      transform: scale(1.3);
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      @include object-fit;
    }
  }
}
