// --------------------------------------------------
// variables
// --------------------------------------------------

$sansSerif: 'Yu Gothic', YuGothic, '游ゴシック', '游ゴシック体', 'メイリオ', Meiryo, 'ヒラギノ角ゴ Pro W3', 'Hiragino Kaku Gothic Pro', 'ＭＳ Ｐゴシック', sans-serif;
$serif: "游明朝", YuMincho, 'Noto Serif JP', "ヒラギノ明朝 ProN W3", "HiraMinProN-W3", "HG明朝E", "ＭＳ Ｐ明朝", "MS PMincho", "MS 明朝", "Sawarabi Mincho", serif;
$monospace: Monaco, Consolas, 'Courier New', Courier, monospace;
$Mplus: 'M PLUS 1p', $sansSerif;
// $Mplus: 'M PLUS Rounded 1c', $sansSerif;
$Noto: 'Noto Sans JP', $sansSerif;
$Sawarabi: "Sawarabi Mincho", $serif;
// $Poiret: 'Poiret One', cursive;
$Poiret: 'Poiret One', cursive;
$EB: 'EB Garamond', $serif;
$MarcellusSC: 'Marcellus SC', $serif;
$Kaushan: 'Kaushan Script', cursive;


// --------------------------------------------------
// mixins
// --------------------------------------------------

@mixin ls($ls:.08) {
  letter-spacing: #{1.0*$ls}em;
  @include ltMD { letter-spacing: #{.8*$ls}em; };
  @include ltSM { letter-spacing: #{.4*$ls}em; };
  @include ltXS { letter-spacing: #{.3*$ls}em; };
  @include ltXXS { letter-spacing: #{.2*$ls}em; };
}

@mixin sansSerif($weight:500, $ls:.08) {
  font-family: $sansSerif;
  font-weight: $weight;
  letter-spacing: #{$ls}em;
};

@mixin serif($weight:400, $ls:.16) {
  font-family: $serif;
  font-weight: $weight;
  letter-spacing: #{$ls}em;
};

@mixin monospace($weight:400, $ls:0) {
  font-family: $monospace;
  font-weight: $weight;
  letter-spacing: #{$ls}em;
};

// @mixin Mplus($weight:500, $ls:.08) {
//   font-family: $Mplus;
//   font-weight: $weight;
//   @include ls($ls);
// };
@mixin Mplus($weight:400, $ls:.04) {
  font-family: $Mplus;
  font-weight: $weight;
  @include ls($ls);
};

@mixin Noto($weight:400, $ls:.08) {
  font-family: $Noto;
  font-weight: $weight;
  letter-spacing: #{$ls}em;
};

@mixin Sawarabi($weight:500, $ls:.16) {
  font-family: $Sawarabi;
  font-weight: $weight;
  letter-spacing: #{$ls}em;
};

@mixin EB($weight:500, $ls:.16) {
  font-family: $EB;
  font-weight: $weight;
  letter-spacing: #{$ls}em;
};

@mixin MarcellusSC($weight:500, $ls:.08) {
  font-family: $MarcellusSC;
  font-weight: $weight;
  letter-spacing: #{$ls}em;
};

@mixin Kaushan($weight:400, $ls:.00) {
  font-family: $Kaushan;
  font-weight: $weight;
  letter-spacing: #{$ls}em;
};

@mixin fs($size, $base: 10) {
  font-size: $size + px;
  font-size: ($size / $base) + rem;
}
