.c-copyright {
  text-align: center;
  border-top: 1px solid rgba($truewhite,.1);
  width: 100%;
  color: $truewhite;
  @include gradbg;

  @include ltXS {
    border-top: none;
  };

  &__item {
    @include fs(14);
    display: inline-block;
    padding-top: 1.5em;
    padding-bottom: 1.5em;
    color: inherit;

    @include ltXS {
      padding-top: 2em;
      padding-bottom: 2em;
    };
  }

  &__link {
    color: $truewhite;
    font-weight: bolder;

    @include hover {
      text-decoration: underline;
    };
  }
}
