.p-dev-notice {
  position: fixed;
  top: 1em;
  left: 1em;
  z-index: 10000;

  @include admin-bar {
    top: calc(32px + 1em);

    @media screen and ( max-width: 782px ) {
      top: calc(46px + 1em);
    }
  };

  &__inner {
    background: $white;
    @include flex(ce,fs);
    position: relative;
  }

  &__icon {
    @include fs(20);
    width: 3.5em;
    height: 3.5em;
    @include flex(ce,ce);
    background: $lightsilver;

    i.fas {
      display: block;
      @include opacity(.5);
    }
  }

  &__body {
    padding: 0 1em;
    position: relative;
  }

  &__close {
    @include fs(12);
    cursor: pointer;
    position: absolute;
    width: 2em;
    height: 2em;
    text-align: center;
    line-height: 2em;
    display: block;
    background: $lightsilver;
    color: $grey;
    top: 0;
    right: 0;
  }

  &__title {
    @include fs(14);
    text-transform: uppercase;
    font-weight: bold;
    @include opacity(.5);
  }

  &__lead {
    @include fs(12);
    font-weight: 500;
  }
}
