.p-agree {
  border: 2px solid $male;
  padding: 2em 3em;
  margin-top: 2em;
  border-radius: 2em;

  @include ltXS {
    margin-top: 1em;
    border-radius: 1em;
    padding: 2em 2em;
  };

  &__accept {
    letter-spacing: 0.05em;
    line-height: 1.6;
    user-select: none;
    margin-top: 1em;

    & > span {
      width: 100%;

      & > span {
        width: 100%;

        & > span {
          width: 100%;
          margin-left: 0 !important;
        }
      }
    }

    label {
      width: 100%;
      max-width: 500px;
      display: block;
      margin: .5em auto;
      background: $lightsilver;
      border-radius: 3em;
      padding: .8em 1em;

      @include ltXXS {
        margin-left: -1.5em;
        margin-right: -1.5em;
        width: calc(100% + 3em);
        letter-spacing: 0;
      };

      input[type='checkbox'] {
        display: none;

        & + span {
          @include flex(ce,fs,false);
          font-weight: 500;
          width: calc( 100% - 5em );
          margin-left: .2em;

          @include ltXS {
            width: calc( 100% - 1em );
          };

          @include glyph-before('\F016') {
            @include flex(ce,ce);
            width: 1.4em;
            margin-right: .5em;
            min-width: 1.4em;
            height: 1.4em;
            background: $white;
            border-radius: 4px;
            color: $white;
            border: 2px solid $female;
            @include fs(22);
            @include ease(.3s);
          };
        }

        &:checked + span::before {
          transform: rotate(360deg);
          box-shadow: 0 0 0 4px $white;
          border-color: $white;
          color: $male;
          border-radius: 50%;
        }
      }

      @include gtSM {
        cursor: pointer;

        &:hover {
          box-shadow: 1px 3px 4px rgba($trueblack,.2);
        }
      };
    }
  }

  &__button-container {
    text-align: center;
    margin: .5em auto;
    position: relative;
    padding-top: 20px;
    padding-bottom: 20px;

    @include ltXXS {
      @include flex(ce,ce,false);
      margin-left: -1em;
      margin-right: -1em;
      width: calc(100% + 2em);
    };
  }

  &__notice {
    color: $female;
    @include fs(20);
    font-weight: 500;
    text-align: center;
    margin-bottom: 1em;
  }

  &__annotation {
    margin-top: 1em;
    @include fs(12);
    line-height: 1.6;
  }
}
